<script>
    import VTag from '@/components/tag/tag';
    import VText from '@/components/tag/text';
    import VTech from '@/components/tag/tech';
    import EventUtil from '@/api/EventUtil.js';
    export default {
        name: 'App',
        data() {
            return {
                exShow: 0,
                show: 0,
                php: {
                    name: 'PHP',
                    list: ['Laravel', 'Mysql', 'Redis', 'Apache', 'Shell', 'ThinkPHP']
                },
                js: {
                    name: '前端',
                    list: ['Vue', 'Git', 'Scss', 'Webpack', 'jQuery', 'AJAX']
                },
                experience: [
                    {
                        company: '重庆速率科技有限公司',
                        time: '2016年1月份-2016年6月份',
                        job: '前端开发',
                        content: [
                            '主要负责网站首页制作，banner图片制作',
                            '技术栈：主要Jquery + Bootstrp',
                            '代表作：<a href="http://www.0856r.com/">速率科技官网</a>',
                            '两年前的作品，不要见怪'
                        ]
                    },
                    {
                        company: '重庆小薇金服有限公司',
                        time: '2016年6月份-2016年11月份',
                        job: 'PHP|前端开发',
                        content: [
                            '主要是Edusoho的系统二次开发',
                            '当时遇到了很多的难点的，刚从学校毕业，没用过的Symfony框架，当时自己对前端require也是懵懵懂懂',
                            '边学边用还是完成的包括积分制，批量操作用户等功能',
                            '现已经停止运作'
                        ]
                    },
                    {
                        company: '深圳烁途科技有限公司',
                        time: '2016年11月份-至今',
                        job: '全栈工程师',
                        content: [
                            '代表作：<a href="https://www.exwayboard.com/index.php/Ch">烁途科技官网</a>  ',
                            '技术栈：前期ThinkPHP+jQuery,后期：vue全家桶+lamp+laravel+redis',
                            '之所以选择换技术栈，因为前期只是单纯的展示型网站，后期还设计了商城，社区功能，为了给自己一些挑战，拓展下自己技术线',
                            '主要任务：基础功能的开发，网站后台采用element-ui+vue',
                            '其它任务：开发APP接口'
                        ]
                    }
                ],
                startY: 0,
                endY: 0,
                startTime: 0,
                going: false,
            };
        },
        created() {
            setTimeout(()=>{
                this.show = 1;
            },300);
            window.onresize = (event) => {
                event = event || window.event;
                if (document.body.clientWidth >= 768) {
                    document.body.scrollTop = 0;
                    document.documentElement.scrollTop = 0;
                } else {
                    this.show = 1;
                }
            }
        },
        mounted() {
            this.$nextTick(() => {
                EventUtil.addHandler(window,'mousewheel',(event) => {
                    console.log('111');
                    event = EventUtil.getEvent(event);
                    if (event.wheelDelta > 0) {
                        this.__swipperUp();
                    } else {
                        this.__swipperDown();
                    }
                });
                EventUtil.addHandler(window,'DOMMouseScroll',(event) => {
                    event = EventUtil.getEvent(event);
                    if (event.detail < 0) {
                        this.__swipperUp();
                    } else {
                        this.__swipperDown();
                    }
                });
//                window.onmousewheel = (event) => {
//                    event = event | window.event;
//                        if (event.wheelDelta > 0) {
//                            this.__swipperUp();
//                        } else {
//                            this.__swipperDown();
//                        }
//                }
            });
        },
        methods: {
            __swipperUp() {

                if (document.body.clientWidth >= 768) {
                    if (this.show > 1 && !this.going) {
                        this.show--;
                        this.going = true;
                        setTimeout(() => {
                            this.going = false;
                        }, 1000);
                    }
                }
            },
            __swipperDown() {
                if (document.body.clientWidth >= 768) {
                    let app = document.getElementById('app');
                    let len = app.children.length;
                    if (this.show < len && !this.going) {
                        this.show++;
                        this.going = true;
                        setTimeout(() => {
                            this.going = false;
                        }, 1000);
                    }
                }
            },
            touchmove (event) {

                if (document.body.clientWidth >= 768) {
                    let moveY = event.changedTouches[0].clientY - this.startY;
                    this.$refs.app.style.marginTop = `${0.8 * moveY}px`;
                }
            },
            touchstart (event) {
                this.startY = event.touches[0].clientY;
                this.startTime = event.timeStamp;
            },
            touchend (event) {
                let moveY = event.changedTouches[0].clientY - this.startY;
                let time = event.timeStamp - this.startTime;
                let margin = parseInt(this.$refs.app.style.marginTop);
                if (time < 1000) {
                    if (moveY < 0) {
                        this.__swipperDown();
                    } else if (moveY > 0){
                        this.__swipperUp();
                    }
                } else {
                    let clientY = document.body.clientHeight;
                    if (clientY/2 < Math.abs(margin)) {
                        if (margin < 0) {
                            this.__swipperDown();
                        } else if (margin > 0) {
                            this.__swipperUp();
                        }
                    }
                }
                this.$refs.app.style.marginTop = '0px'
            },
            select(index) {
                this.exShow = index;
            }
        },
        components: {
            VTag, VText, VTech
        }
    }
</script>

