<template>

    <!-- -->
    <div class="tec">
        <div class="tec-border"></div>
        <div class="tec-name">{{tecName.name}}</div>
        <span class="tec-item" v-for="(item,index) in tecName.list" :style="'transform-origin:center 150px;transform:translateX(-50%) rotate('+ 360/tecName.list.length * index+'deg)'"><span :class="'p-icon'+index">{{item}}</span></span>
    </div>
</template>
<script>
    export default {
        name: 'tech',
        props: {
            tecName: {
                type: Object,
                default () {
                    return {};
                }
            }
        }
    }
</script>
<style lang="scss">
.tec{
    width: 300px;
    height: 300px;
    position: relative;
    padding: 10px;
    .tec-border{
        width: 100%;
        height: 100%;
        border:3px dashed #e2e2e2;
        border-radius: 50%;
    }
    animation: phpRoute 12s linear infinite;
    .tec-name{
        font-size: 24px;
        position: absolute;
        top:50%;
        left: 50%;
        color: #000;
        transform: translate(-50%,-50%);
        animation: nameRoute 12s linear 0s infinite;
    }
    .tec-item{
        display: inline-block;
        position: absolute;
        font-size: 16px;
        left: 50%;
        top:0;

        .p-icon0{
            display: inline-block;
            transform: rotate(0deg);
            padding: 5px;
            background: #fff;
            color: #000;
            border-radius: 4px;
            animation: itemRoute 12s linear infinite;
        }
        .p-icon1{
            display: inline-block;
            padding: 5px;
            background: #fff;
            color: #000;
            border-radius: 4px;
            animation: itemRoute 12s linear -2s infinite;
        }
        .p-icon2{
            display: inline-block;
            transform: rotate(-120deg);
            padding: 5px;
            background: #fff;
            color: #000;
            border-radius: 4px;
            animation: itemRoute 12s linear -4s infinite;
        }
        .p-icon3{
            display: inline-block;
            transform: rotate(-180deg);
            padding: 5px;
            background: #fff;
            color: #000;
            border-radius: 4px;
            animation: itemRoute 12s linear -6s infinite;
        }
        .p-icon4{
            display: inline-block;
            transform: rotate(-240deg);
            padding: 5px;
            background: #fff;
            color: #000;
            border-radius: 4px;
            animation: itemRoute 12s linear -8s infinite;
        }
        .p-icon5{
            display: inline-block;
            transform: rotate(-300deg);
            padding: 5px;
            background: #fff;
            color: #000;
            border-radius: 4px;
            animation: itemRoute 12s linear -10s infinite;
        }
    }
}
@keyframes phpRoute {
    from{
        transform: rotate(0deg);
    } to {
          transform: rotate(360deg);
      }
}
@keyframes itemRoute {
    from{
        transform: rotate(0deg);
    } to {
          transform: rotate(-360deg);
      }
}
@keyframes nameRoute {
    from{
        transform:translate(-50%,-50%) rotate(0deg);
    } to {
          transform:translate(-50%,-50%) rotate(-360deg);
      }
}

</style>



