<template>
    <div class="self-text">
        <p>2016年毕业于吉林大学珠海学院</p>
        <p>工作期间一直从事于Web开发方面，包括前后端</p>
        <p>JS的日新月异让我一直保持前进的状态</p>
        <p>同事寻找后端的各种解决方案使我受益颇多</p>
        <p>学习本无底，前进莫徬徨</p>
    </div>
</template>
<script>
    export default {
        name:　'vtext'
    }
</script>
<style lang="scss">
    .self-text{
        max-width: 900px;
        margin: 5vh auto 0px auto;
        font-size: 14px;
        text-align: center;
        line-height: 25px;
        letter-spacing: 1px;
        position: relative;
        padding: 10px;
        p{
            &:first-child:before{
                content: "";
                display: inline-block;
                position: absolute;
                top:0px;
                left: 0px;
                width:50px;
                height: 50px;
                border-left: 2px solid rgba(107,184,190, 1);
                border-top: 2px solid rgba(107,184,190, 1);
            }
            &:last-child:after{
                content: "";
                display: inline-block;
                position: absolute;
                bottom:0px;
                right: 0px;
                width:50px;
                height: 50px;
                border-right: 2px solid rgba(107,184,190, 1);
                border-bottom: 2px solid rgba(107,184,190, 1);
            }
        }
    }
</style>


